* {
  box-sizing: border-box;
}

body {
  color: #212121;
  background-color: #fdf6e2;
  margin: 0;
  line-height: 1.5;
}

.search-form input {
  height: 40px;
  min-width: 300px;
  border: none;
  border-radius: 4px;
  outline: none;
  margin: 0;
}

.search-form button {
  cursor: pointer;
  height: 40px;
  border: none;
  border-radius: 4px;
  margin: 0;
  position: relative;
  right: 10px;
}

.search-form button:hover {
  box-shadow: inset 0 1px 6px rgba(26, 26, 37, .5);
}

.header-styles {
  height: 60px;
  background-color: #335ce2;
  background-image: linear-gradient(to top, rgba(46, 47, 66, .7), rgba(82, 85, 141, .7));
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 1px 6px rgba(26, 26, 37, .5);
}

.load-more {
  text-align: center;
  letter-spacing: .04em;
  color: #fff;
  width: 200px;
  height: 40px;
  background-color: #335ce2;
  background-image: linear-gradient(to top, rgba(46, 47, 66, .7), rgba(82, 85, 141, .7));
  margin-bottom: 50px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
  transition-property: background-color;
  transition-duration: .2s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 1px 6px rgba(26, 26, 37, .5);
}

.load-more:hover {
  background: #335ce2;
  box-shadow: inset 0 1px 6px rgba(26, 26, 37, .5);
}

.hidden {
  visibility: hidden;
}

.gallery {
  flex-wrap: wrap;
  gap: 48px 24px;
  padding: 50px 0;
  display: flex;
}

.photo-conteiner {
  width: 350px;
  height: 225px;
  align-items: center;
  display: flex;
}

.photo-card {
  box-sizing: border-box;
  background-color: rgba(249, 250, 232, .827);
  border: 1px solid #e7e9fc;
  align-items: center;
  margin: 0 auto;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08);
}

.photo-card img {
  width: 100%;
  max-height: 225px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.info {
  justify-content: space-between;
  margin-bottom: 0;
  padding: 0 20px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}



